.App-logo {
  height: 40vmin;
  pointer-events: none;
}

html {
  font-size: 62.5%;
  /* background-color: rgb(0, 0, 0);
  filter: invert(1) hue-rotate(180deg); */
}

h3 {
  font-size: 2.8rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  width: 100%;
  display: flex;
  background: linear-gradient(
    to right,
    rgba(16, 179, 255, 0.404),
    rgba(22, 160, 224, 0.698),
    rgba(16, 179, 255, 0.404)
  );
  color: rgba(0, 0, 0, 0.883);
  justify-content: space-between;
  align-items: center;
  height: 60px;
}

.header-title {
  margin: 0 0 0 20px;
  font-family: Monospace, Pacifico, "Abril Fatface";
  text-align: left;
  height: 100%;
  font-size: 2.5rem;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;
}

.header-content {
  box-sizing: border-box;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.header-content h3 {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 20px 0 0;
  height: 100%;
}

.header-content h3:hover {
  background-color: #13c9fb;
  cursor: pointer;
}

.container {
  width: 100%;
}

.setting-container {
  min-width: 950px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  height: 45px;
  /* background-color: rgba(149, 144, 144, 0.459); */
  padding-top: 30px;
  padding-bottom: 30px;
}

.sorting-speed {
  width: 250px;
}

.sorting-speed label[for="speed"] {
  margin-right: 10px;
  font-weight: 800;
  font-size: 1.5rem;
  width: 50px;
  box-sizing: border-box;
}

.select-class {
  width: 200px;
  font-size: 1.8rem;
}

.sorting-speed input[type="range"] {
  width: 200px;
  box-sizing: border-box;
  height: 25px;
  -webkit-appearance: none;
  margin: 10px 0;
}
.sorting-speed input[type="range"]:focus {
  outline: none;
}
.sorting-speed input[type="range"]::-webkit-slider-runnable-track {
  height: 5px;
  cursor: pointer;
  background: rgb(119, 184, 228);
  border-radius: 1px;
}

.sorting-speed input[type="range"]::-webkit-slider-thumb {
  border: 1px solid rgb(119, 184, 228);
  height: 25px;
  width: 25px;
  border-radius: 25px;
  background: #a1d0ff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -7px;
}

.sorting-speed input[type="range"]:focus::-webkit-slider-runnable-track {
  background: rgb(119, 184, 228);
}

.setting-container > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.sorting-speed input[type="range"]::-moz-range-track {
  height: 5px;
  cursor: pointer;
  background: rgb(119, 184, 228);
  border-radius: 1px;
}
.sorting-speed input[type="range"]::-moz-range-thumb {
  border: 1px solid rgb(119, 184, 228);
  height: 25px;
  width: 25px;
  border-radius: 25px;
  background: #a1d0ff;
  cursor: pointer;
}
.sorting-speed input[type="range"]::-ms-track {
  height: 5px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  color: transparent;
}
.sorting-speed input[type="range"]::-moz-focus-outer {
  border: 0;
}

.sorting-speed input[type="range"]::-ms-fill-lower {
  background: rgb(119, 184, 228);
  border-radius: 2px;
}
.sorting-speed input[type="range"]::-ms-fill-upper {
  background: rgb(119, 184, 228);
  border-radius: 2px;
}
.sorting-speed input[type="range"]::-ms-thumb {
  margin-top: 1px;
  border: 1px solid rgb(119, 184, 228);
  height: 25px;
  width: 25px;
  border-radius: 25px;
  background: #a1d0ff;
  cursor: pointer;
}
.sorting-speed input[type="range"]:focus::-ms-fill-lower {
  background: rgb(119, 184, 228);
}
.sorting-speed input[type="range"]:focus::-ms-fill-upper {
  background: rgb(119, 184, 228);
}

.input-size,
.max-number {
  position: relative;
}

.input-size input:focus,
.max-number input:focus {
  outline: 0;
}

.input-size input[type="text"],
.max-number input[type="text"] {
  font-size: 1.6rem;
  width: 100%;
  border: none;
  padding-top: 20px;
}

.input-size input::-webkit-inner-spin-button,
.input-size input::-webkit-outer-spin-button,
.max-number input::-webkit-inner-spin-button,
.max-number input::-webkit-outer-spin-button {
  -webkit-appearance: none;
}

.input-size input[type="number"],
.max-number input[type="number"] {
  -moz-appearance: textfield;
}

.input-size label,
.max-number label {
  position: absolute;
  bottom: 0;
  text-align: left;
  pointer-events: none;
  left: 0;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid rgb(119, 184, 228);
  -webkit-box-shadow: 0px 6px 0px 0px rgba(113, 184, 231, 0.39);
  -moz-box-shadow: 0px 6px 0px 0px rgba(119, 184, 231, 0.39);
  box-shadow: 0px 6px 0px 0px rgba(119, 184, 231, 0.39);
}

.input-size input[type="text"]::-webkit-inner-spin-button,
.input-size input[type="text"]::-webkit-outer-spin-button,
.max-number input[type="text"]::-webkit-inner-spin-button,
.max-number input[type="text"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.setting-container label {
  margin-right: 10px;
  box-sizing: border-box;
  font-size: 1.3rem;
  font-weight: 800;
}

.setting-container button {
  font-weight: 800;
  font-size: 1.6rem;
  border: 0;
  padding: 8 8 8 8;
  height: 100%;
  border-radius: 5px;
  color: white;
  background-color: rgb(119, 184, 228);
}
.setting-container button:hover {
  cursor: pointer;
  background-color: rgba(135, 191, 228, 0.945);
}
.sorting-button {
  display: flex;
  flex-direction: row-reverse;
  flex-flow: row-reverse;
  justify-content: space-around;
}

.sorting-button button {
  font-weight: 800;
  font-size: 1.6rem;
  border: 0;
  padding: 8 8 8 8;
  height: 45px;
  border-radius: 5px;
  color: white;
  background-color: rgb(119, 184, 228);
}
.sorting-button button:hover {
  cursor: pointer;
  background-color: rgba(135, 191, 228, 0.945);
}

.bar-chart {
  height: 70vh;
  min-height: 500px;
  margin-bottom: 10px;
  width: 100%;
  min-width: 1000px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}

.bar-chart div {
  display: inline-block;
  bottom: 0;
  margin: 0;
  background: linear-gradient(to bottom, rgba(90, 90, 90, 0.9), 80%,rgba(90, 90, 90, 0.3));
  box-sizing: border-box;
  line-height: 100%;
}
button:focus {
  outline: 0;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 6rem;
  text-align: center;
  /* display: none; */
  opacity: 0;
  -webkit-filter: blur(0px);
  filter: blur(0px);
  height: 0px;
  -moz-transition: opacity 1s ease-in-out;
  -o-transition: opacity 1s ease-in-out;
  -webkit-transition: opacity 1s ease-in-out;
  transition: opacity 1s ease-in-out;
}

.modalBlur :not(.modal) {
  -webkit-filter: blur(8px);
  filter: blur(8px);
}
.modal-backdrop {
  opacity: 0.65;
  filter: alpha(opacity=65);
}
